.wrapper {
  /* display: flex;
  flex-wrap: wrap; */
}

h1 {
  font-family: 'Sarabun', sans-serif;
  color: white;
  text-align: center
  
}

#nav {
  min-height: 50px;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  background-color: rgb(213, 68, 68);
  padding: 10px;
  align-items: center;
  justify-content: space-around;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, .8);
}

.nav-text {
  font-family: "Sarabun", sans-serif;
  font-weight: 400;
  padding: 5px;
  margin-left: 10px;
  font-size: 1.25rem;
  color: white;
}

.logoDiv {
  display: flex;
  align-content: center;
}
/* "ClickyGame" */
.logo {
font-weight: 900;
font-size: 2rem;
line-height:100%;
}
/* Pointer Image */
#logo {
  height: 2rem;
  margin-left: 1rem;
}

.jumboTron {
  display: flex;
  min-height: 250px;
  background-color: lightgray;
  background: -webkit-linear-gradient(rgba(128, 136, 174, 0.9), rgba(128, 136, 174, 0.9)),url(/React-Clicky-Game-New/static/media/abstract-art-background-430207.fbc8781a.jpg) 
   center center fixed;
  background: linear-gradient(rgba(128, 136, 174, 0.9), rgba(128, 136, 174, 0.9)),url(/React-Clicky-Game-New/static/media/abstract-art-background-430207.fbc8781a.jpg) 
   center center fixed;
   background-color: grey;
  background-blend-mode: multiply;
  background-size: cover;
  background-repeat: no-repeat;
  align-content: center;
  /* background-color: black; */
}

.footer {
  background-color:  rgb(181, 27, 27);
  width: 100%;
  height: 50px;
  display: flex;
  align-items: bottom;
  position: fixed;
  left: 0;
  bottom: 0;
}
/* Couldn't make the sub footer fix to the bottom in the footer come back later*/
.subFooter {
  background-color:  rgb(213, 68, 68);
  width: 100%;
  height: 25px;
  padding: 5px;
}

.charWrap {
display: flex;
flex-wrap: wrap;
width: 70%;
margin-top: 2%;
margin-right: auto;
margin-left: auto;
justify-content: space-around;
}

.charPicContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  width: 100px;
  height: 100px;
  margin: 10px;
  box-shadow: 5px 5px 8px #888888;
  
}

.charPicContainer img {
  flex-shrink: 0;
  max-height: 100%;
  max-width: 100%;
}





/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
